<template>
  <div
    class="ball-container ball__shadow"
    :class="{'ball__shadow--hover': ballHover}"
    ref="ball"
    :style="{left:left+'px',top:top+'px',zIndex:zIndex}"
    @mouseenter="stop"
    @mouseleave="start"
  >
    <router-link :to="{name:linkName}">
      <div>{{text}}</div>
    </router-link>
  </div>
</template>

<script>
import getRandom from "@/util/getRandom";
export default {
  props: ["text", "linkName"],
  name: "Ball",
  mounted() {
    this.aboutContainer = this.$refs.ball.parentElement;
    this.top = getRandom(0, this.aboutContainer.clientHeight - 150);
    this.left = getRandom(0, this.aboutContainer.clientWidth - 150);
    this.start();
  },
  data() {
    return {
      aboutContainer: null,
      top: 0,
      left: 0,
      timer: null,
      speedX: getRandom(20, 50),
      speedY: getRandom(20, 50),
      zIndex: "",
      ballColor: ["red", "green", "brown", "blue"],
      ballHover: false,
    };
  },
  methods: {
    start() {
      this.zIndex = "";
      this.ballHover = false;
      const duration = 16;
      if (this.timer) {
        clearInterval(this.timer);
      }
      this.timer = setInterval(() => {
        const disX = (this.speedX * duration) / 1000;
        const disY = (this.speedY * duration) / 1000;
        this.top += disY;
        this.left += disX;

        if (this.top > this.aboutContainer.clientHeight - 150) {
          this.top = this.aboutContainer.clientHeight - 150;
          this.speedY = -this.speedY;
        }
        if (this.top < 0) {
          this.top = 0;
          this.speedY = -this.speedY;
        }
        if (this.left > this.aboutContainer.clientWidth - 150) {
          this.left = this.aboutContainer.clientWidth - 150;
          this.speedX = -this.speedX;
        }
        if (this.left < 0) {
          this.left = 0;
          this.speedX = -this.speedX;
        }
      }, duration);
    },
    stop() {
      clearInterval(this.timer);
      this.zIndex = 999;
      this.ballHover = true;
    },
  },
  computed: {
    className() {
      return "ball__shadow--" + this.ballColor[getRandom(0, 3)];
    },
  },
  beforeDestroy() {
    this.stop();
  },
};
</script>

<style scoped>
.ball-container {
  position: absolute;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  text-align: center;
  line-height: 150px;
  cursor: pointer;
  user-select: none;
  font-weight: bolder;
  font-size: 20px;
  color: #fff;
}
.ball__shadow {
  box-shadow: 1px 1px 10px 0 #fff, inset 1px 1px 10px 0 #fff,
    inset 1px 1px 10px 0 #fff, inset -1px -1px 20px 0 #fff,
    inset -1px -1px 20px 0 #fff;
}
.ball__shadow--hover {
  box-shadow: 1px 1px 10px 0 #fff, 0px 0px 10px 0 #fff,
    inset 1px 1px 10px 0 #fff, inset 1px 1px 10px 0 #fff,
    inset -1px -1px 20px 0 #fff, inset -1px -1px 20px 0 #fff;
}
</style>