import Home from "@/views/Home";
import About from "@/views/About";
import Blog from "@/views/Blog";
import Message from "@/views/Message";
import Project from "@/views/Project";
// import BlogDetail from "@/views/Blog/Detail";

export default [
    { name: "Home", path: "/", component: Home, meta: { title: '首页' } },
    { name: "About", path: "/about", component: About, meta: { title: '关于我', transition: 'slide-left' } },
    { name: "BaseInfo", path: "/about/baseInfo", component: () => import('@/views/About/components/BaseInfo'), meta: { title: '基本信息', transition: 'slide-right' } },
    // { name: "Blog", path: "/blog", component: Blog, meta: { title: '文章' } },
    { name: "MyBlog", path: "/myBlog", component: () => import('@/views/MyBlog'), meta: { title: '我的文章' } },
    // { name: "CategoryBlog", path: "/blog/cate/:categoryId", component: Blog },
    { name: "CategoryBlog", path: "/myBlog/cate/:categoryId", component: () => import('@/views/MyBlog'), meta: { title: '我的文章' } },
    // { name: "BlogDetail", path: "/blog/:id", component: BlogDetail, meta: { title: '文章详情' } },
    { name: "BlogDetail", path: "/myBlog/", component: () => import('@/views/MyBlog/components/BlogDetail'), meta: { title: '文章详情' } },
    { name: "Message", path: "/message", component: Message, meta: { title: '留言板' } },
    { name: "Project", path: "/project", component: Project, meta: { title: '项目&效果' } }
]