for (let i = 0; i < type.length; i++) {
    const element = type[i];
    blog.forEach(ele => {
        if (ele.category.id === element.id) {
            element.articleCount++;
        }
    })

}


Mock.mock("/api/blogtype", "get", {
    code: 0,
    msg: "",
    data: type
})


Mock.mock(/^\/api\/blog(\?.+)?$/, "get", function (options) {
    const params = options.url.substring(options.url.indexOf("?") + 1);
    const obj = paramsToObj(params);
    let res = blog;
    if (obj.categoryId != -1) {
        res = blog.filter(item => {
            console.log(item.category.id === obj.categoryId);
            return item.category.id === +obj.categoryId
        })
    }
    let len = res.length
    res = res.slice((obj.page - 1) * obj.limit, obj.page * obj.limit);
    return Mock.mock({
        code: 0,
        msg: "",
        data: {
            "total": len,
            "rows": res
        }
    })
})

